.section__title {
  margin: 0;
  margin-bottom: 48px;
  font-size: 32px;
  font-weight: 500;
  letter-spacing: -1.5px;
  text-align: center;

  &--align--left {
    text-align: left;
    margin-bottom: 32px;
  }

  @include on-tablet {
    font-size: 48px;
    margin-bottom: 48px;
  }

  @include on-desktop {
    font-size: 64px;
    margin-bottom: 56px;
  }
}
